<template lang="pug">
    .test__point 
        .test__question {{index+1}}. 
            vue-mathjax(:formula="formula")
        ul.test__answers
            li.test__answer(v-for="answer,idx in point.testAnswers" :key="answer.id")
                label.test__answer-text {{answerChars[idx]}}) {{point.testAnswers[idx]}}
                input(@change='checkAnswer' type='radio' :value='answerChars[idx]' :name="index" v-model='testInput').test__input
</template>

<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax
    },
    props: {
        point: Object,
        index: Number 
    },
    data() {
        return {
            testInput: '',
            formula: this.point.testQuestion,
            answerChars: ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h']
        }
    },
    mounted() {
        let recaptchaScript = document.createElement('script')
        recaptchaScript.setAttribute('src', 'https://cdnjs.cloudflare.com/ajax/libs/mathjax/2.7.2/MathJax.js?config=TeX-AMS_HTML')
        document.head.appendChild(recaptchaScript)
    },
    methods: {
        checkAnswer() {
            if(this.testInput == this.point.testAnswer) {
                const local = JSON.stringify({
                    index: this.index,
                    answer: this.testInput
                })
                localStorage.setItem(this.index, local) 
            }
        }
    }
}
</script>
<style lang="scss">
.mjx-chtml{ font-size: 100% !important; }
.test__point {
    border-bottom: dotted rgba($color: black, $alpha: 0.4);
    margin-bottom: 2%;
}
.test__question {
    font-family: 'Alegreya Sans Extra Bold';
    padding: 1%;
}
.test__answer{
    padding: 1%;
    display: flex;
}
.test__answer-text {
    margin-right: 5%;
    width: 40%;
}
</style>